@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(rgb(47, 158, 106)), to(rgb(148, 93, 67)));
  background: linear-gradient(90deg, rgb(47, 158, 106) 0%, rgb(148, 93, 67) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Tilt {
    background: -webkit-gradient(linear, left top, right top, from(rgb(47, 158, 106)), to(rgb(148, 93, 67)));
    background: linear-gradient(90deg, rgb(47, 158, 106) 0%, rgb(148, 93, 67) 100%);
}
.form {
    width: 700px;
    background-color:#001;
    background-image: radial-gradient(white 15%, transparent 16%),
    radial-gradient(white 15%, transparent 16%);
    background-size: 60px 60px;
    background-position: 0 0, 30px 30px;
}

button {
    cursor: pointer;
    background-color: rgb(148, 93, 67);
}
.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #fae41a inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}
.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -2;
}
